import { get_contract_thunk } from './../../../redux/actions/actions';
import { useEffect, useState } from 'react';
import { buildStyles } from 'react-circular-progressbar';
import { useTranslation } from 'react-i18next';
import { useDispatch, useSelector } from 'react-redux';
import { contract } from '../../../web3/contract-api';
import { RootState } from '../../../redux/store';
import { num } from '../../../resources/constants';

export const useDashboard = () => {
  const [supply, setSupply] = useState(0);
  const [rewardPerToken, setRewardPerToken] = useState(0);
  const [isError, setIsError] = useState('');
  const [conn, setConn] = useState(false);
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const data = useSelector((state: RootState | any) => state.current_contract.current_contract);
  const build_styles = buildStyles({
    // strokeLinecap: 'butt',
    textColor: '#64a640',
    pathColor: '#64a640',
    trailColor: '#c6b5a4',
  });
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  useEffect(() => {
    const intervalContract = setInterval(() => {
      !conn &&
        contract.connect().then(
          res => {
            setConn(true);
            dispatch(get_contract_thunk(res));
            setIsError('');
          },

          error => setIsError(error.message),
        );
    }, 2000);
    return () => clearInterval(intervalContract);
  });

  if (typeof data.contract !== 'undefined') {
    data.contract.totalSupply().then(
      (res: any) => setSupply(res.div(num).toString()),
      (error: { message: any }) => error.message,
    );

    data.contract.rewardPerToken().then(
      (res: any) => setRewardPerToken(res.div(num).toString()),
      (error: { message: any }) => error.message,
    );
  }

  const rewardNum = +rewardPerToken;
  const supplyNum = +supply;
  const availableRewards = 100000 - supplyNum * rewardNum;
  const antail = supplyNum / rewardNum;

  let percentage = rewardNum === 0 && supplyNum === 0 ? (100000 - supplyNum * rewardNum) / 100000 : 0;

  return { build_styles, percentage, data, supply, supplyNum, isError, conn, availableRewards, antail, t };
};
